<template>
  <div>
    <v-row>
      <v-col cols="12">
        <v-card>
          <v-card-title flat>
           
            <v-toolbar-title>   <v-icon @click="returnClick()" >mdi-arrow-left</v-icon> Ordens do Cliente</v-toolbar-title>
          </v-card-title>
          <v-card-text>
            <v-row>
              <v-col>
                <v-icon class="mr-2"> mdi-account </v-icon> {{ customer.name }}
              </v-col>
            </v-row>

            <v-row>
              <v-col cols="12" sm="6" xs="6" md="4">
                <v-icon class="mr-2"> mdi-phone </v-icon>
                {{ customer.phone }}
              </v-col>

              <v-col cols="12" sm="6" xs="6" md="4">
                <v-icon class="mr-2"> mdi-email </v-icon>
                {{ customer.email }}
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-tabs v-model="activeTab">
      <v-tab>Ordens</v-tab>
      <v-tab>Faturas</v-tab>
      <v-tab-item :key="1">
        <v-row>
          <v-col cols="12">
            <v-btn
              v-if="selectedsToInvoice.length < 1"
              fab
              color="red accent-1"
              right
              absolute
              @click="add()"
            >
              <v-icon>mdi-plus</v-icon>
            </v-btn>
            <v-btn
              v-else
              fab
              color="blue accent-1"
              right
              absolute
              @click="toInvoiceClick()"
            >
              <v-icon>mdi-receipt</v-icon>
            </v-btn>
            <v-card>
              <v-card-text>
                <v-card-subtitle class="headline">Ordens</v-card-subtitle>
                <v-row>
                  <v-col cols="6" sm="6" md="3">
                    <v-select
                      v-model="filter.status"
                      multiple
                      :clearable="true"
                      :items="filter.statusList"
                      label="Situao"
                    ></v-select>
                  </v-col>

                  <v-col cols="6" md="2">
                    <v-select
                      v-model="filter.billingStatus"
                      :clearable="true"
                      :items="filter.billingStatusList"
                      label="Sit Faturamento"
                    ></v-select>
                  </v-col>
                  <v-col cols="5" md="3">
                    <v-dialog
                      ref="dialogConclusionIni"
                      v-model="modalConclusionIni"
                      :return-value.sync="filter.dtConclusionIni"
                      persistent
                      width="290px"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                          v-model="computedConclusionIniFormatted"
                          label="Data Inicial de Concluso"
                          prepend-icon="mdi-calendar"
                          readonly
                          v-bind="attrs"
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                        v-model="filter.dtConclusionIni"
                        locale="pt-br"
                        scrollable
                      >
                        <v-spacer></v-spacer>

                        <v-btn
                          text
                          color="warning"
                          @click="filter.dtConclusionIni = null"
                        >
                          limpa
                        </v-btn>

                        <v-btn
                          text
                          color="primary"
                          @click="modalConclusionIni = false"
                        >
                          Cancela
                        </v-btn>
                        <v-btn
                          text
                          color="primary"
                          @click="
                            $refs.dialogConclusionIni.save(
                              filter.dtConclusionIni
                            )
                          "
                        >
                          OK
                        </v-btn>
                      </v-date-picker>
                    </v-dialog>
                  </v-col>
                  <v-col cols="5" md="3">
                    <v-dialog
                      ref="dialogConclusionEnd"
                      v-model="modalConclusionEnd"
                      :return-value.sync="filter.dtConclusionEnd"
                      persistent
                      width="290px"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                          v-model="computedConclusionEndFormatted"
                          label="Data Final de Concluso"
                          prepend-icon="mdi-calendar"
                          readonly
                          v-bind="attrs"
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                        v-model="filter.dtConclusionEnd"
                        locale="pt-br"
                        scrollable
                      >
                        <v-spacer></v-spacer>

                        <v-btn
                          text
                          color="warning"
                          @click="filter.dtConclusionEnd = null"
                        >
                          limpa
                        </v-btn>

                        <v-btn
                          text
                          color="primary"
                          @click="modalConclusionEnd = false"
                        >
                          Cancela
                        </v-btn>
                        <v-btn
                          text
                          color="primary"
                          @click="
                            $refs.dialogConclusionEnd.save(
                              filter.dtConclusionEnd
                            )
                          "
                        >
                          OK
                        </v-btn>
                      </v-date-picker>
                    </v-dialog>
                  </v-col>

                  <v-col cols="1" md="1">
                    <v-btn @click="onClickFilter()" text
                      ><v-icon>mdi-magnify</v-icon></v-btn
                    >
                  </v-col>
                </v-row>
                <v-list>
                  <!-- <v-list-item-group> -->
                  <!-- <v-list-item-group v-model="selectedsToInvoice" multiple> -->
                  <template v-for="(item, index) in orders">
                    <v-list-item
                      :key="item.id"
                      :value="item.id"
                      active-class="deep-purple--text text--accent-4"
                      :disabled="item.billingStatus == 'Faturado'"
                    >
                      <v-list-item-content>
                        <v-list-item-title
                          v-text="item.title"
                        ></v-list-item-title>
                        <v-list-item-content>
                          <v-col>
                            <v-list-item-subtitle
                              class="text--primary"
                              v-text="item.protocol"
                            ></v-list-item-subtitle>
                            <v-list-item-subtitle
                              v-text="item.type"
                            ></v-list-item-subtitle>
                            <v-list-item-subtitle
                              v-text="'Sit: ' + item.status"
                            ></v-list-item-subtitle>
                          </v-col>
                          <v-col>
                            <v-list-item-subtitle
                              v-text="'Inicio: ' + formatDate(item.creation)"
                            ></v-list-item-subtitle>
                            <v-list-item-subtitle
                              v-text="'Final: ' + formatDate(item.conclusion)"
                            ></v-list-item-subtitle>
                          </v-col>
                          <v-col>
                            <v-list-item-subtitle
                              v-text="'$$ ' + item.billingStatus"
                            ></v-list-item-subtitle>
                            <v-list-item-subtitle
                              v-text="'Preo: ' + formatDecimal(item.price)"
                            ></v-list-item-subtitle>
                          </v-col>
                        </v-list-item-content>
                      </v-list-item-content>

                      <v-list-item-action v-if="item.billingStatus == 'Aberto'">
                        <v-checkbox
                          @click="selectItem(item)"
                          v-if="item.billingStatus == 'Aberto' && item.status == 'Final'"
                          color="deep-purple accent-4"
                        ></v-checkbox>
                      </v-list-item-action>
                      <v-list-item-action v-if="item.billingStatus == 'Aberto'">
                        <v-icon small @click="edit(item)" class="mr-2" v-show="item.billingStatus == 'Aberto'">
                          mdi-pencil
                        </v-icon>
                      </v-list-item-action >
                      <v-list-item-action v-if="item.billingStatus == 'Faturado'">
                        Faturado | {{item.invoice.uid}}
                      </v-list-item-action>
                      <!-- </template> -->
                    </v-list-item>

                    <v-divider v-if="index + 1 < orders.length" :key="index">
                    </v-divider>
                  </template>
                  <v-list-item>
                    <v-row>
                      <v-col cols="12" sm="12" xs="12" md="12">
                        <div class="float-right">
                          <v-icon class="mr-2"> mdi-currency-usd</v-icon>Total:
                          {{ formatDecimal(total) }}
                        </div>
                      </v-col>
                    </v-row>
                  </v-list-item>
                  <!-- </v-list-item-group> -->
                </v-list>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-tab-item>
      <v-tab-item :key="2">
        <v-row>
          <v-col cols="12">
            <invoice v-bind:customer="customer"></invoice>
          </v-col>
        </v-row>
      </v-tab-item>
    </v-tabs>
    <order v-bind:order="editItem" v-bind:activate.sync="showOrder"></order>
  </div>
</template>
<style type="text/css">
#lateral .v-btn--example {
  bottom: 0;
  position: absolute;
  margin: 0 0 16px 16px;
}
</style>

<script>
import firebase from "firebase";
import order from "./Order.vue";
import moment from "moment";
import InvoiceService from "../services/invoice-service";
import invoice from "./Invoice.vue";
export default {
  name: "CustomerOrders",

  components: {
    order,
    invoice,
  },

  data() {
    return {
      activeTab: 0,
      headers: [
        { text: "Nome", value: "name", align: "left" },
        { text: "Telefone", value: "phone", align: "left" },
        { text: "E-mail", value: "email", align: "left" },
        { text: "ID", value: "id", align: "left" },
        { text: "Aes", value: "actions", sortable: false },
      ],
      customer: {},
      orders: [],
      item: {},
      editItem: {},
      showOrder: false,

      filter: {
        title: "",
        protocol: "",
        status: [],
        type: "",
        billingStatus: "Aberto",
        statusList: ["Aberto", "A. Doc", "Andamento", "Final"],
        billingStatusList: ["Aberto", "Faturado"],
        types: ["Constituio", "Transformao", "Extinso", "Ciso"],
        dtConclusionIni: null,
        dtConclusionEnd: null,
      },

      total: 0,
      selectedsToInvoice: [],
      ordersToInvoice: [],
      modalConclusionIni: false,
      modalConclusionEnd: false,
    };
  },

  computed: {
    computedConclusionIniFormatted() {
      return this.formatDate(this.filter.dtConclusionIni);
    },
    computedConclusionEndFormatted() {
      return this.formatDate(this.filter.dtConclusionEnd);
    },
  },

  created() {
    if (this.$route.params.customerId) {
      let customerId = this.$route.params.customerId;
      this.loadCustomer(customerId);
      this.getOrders(customerId);
    } else {
      alert("Cliente sem id.");
    }
  },

  watch: {
    selectedsToInvoice(val) {
      const retToInvoice = [];
      const self = this;

      val.forEach((value) => {
        const filteredOrder = self.orders.find((order) => order.id == value.id);

        if (filteredOrder?.billingStatus == "Aberto")
          retToInvoice.push(value.id);
      });

      this.ordersToInvoice = retToInvoice;
    },
  },

  methods: {
    selectItem(item) {
      if (!this.selectedsToInvoice.includes(item))
        this.selectedsToInvoice.push(item);
      else
        this.selectedsToInvoice.splice(
          this.selectedsToInvoice.indexOf(item),
          1
        );
    },

    loadCustomer(customerId) {
      var docRef = firebase.firestore().collection("customer").doc(customerId);

      let t = this;

      docRef
        .get()
        .then(function (doc) {
          if (doc.exists) {
            t.customer = doc.data();
            t.customer.id = doc.id;
          } else {
            console.log("Cliente no encontrado!");
          }
        })
        .catch(function (error) {
          console.log("Error getting document:", error);
        });
    },

    onClickFilter() {
      this.getOrders(this.customer.id);
    },

    addFilters(query) {
      if (this.filter.status.length > 0) {
        query = query.where("status", "in", this.filter.status);
      }
      if (this.filter.type && this.filter.type.length > 0) {
        query = query.where("type", "==", this.filter.type);
      }
      if (this.filter.billingStatus && this.filter.billingStatus.length > 0) {
        query = query.where("billingStatus", "==", this.filter.billingStatus);
      }
      if (this.filter.protocol.length > 0) {
        query = query
          .orderBy("protocol")
          .where("protocol", ">=", this.filter.protocol)
          .where("protocol", "<=", this.filter.protocol + "~");
      }
      if (this.filter.title.length > 0) {
        query = query
          .orderBy("title")
          .where("title", ">=", this.filter.title)
          .where("title", "<=", this.filter.title + "~");
      }
      if (this.filter.dtConclusionIni && this.filter.dtConclusionEnd) {
        query = query
          .orderBy("conclusion")
          .where("conclusion", ">=", this.filter.dtConclusionIni)
          .where("conclusion", "<=", this.filter.dtConclusionEnd);
      }
      return query;
    },

    getOrders(customerId) {
      this.orders = [];

      let custRef = firebase.firestore().collection("customer").doc(customerId);

      let query = firebase
        .firestore()
        .collection("order")
        .where("customer", "==", custRef);

      query = this.addFilters(query);

      query.onSnapshot((snap) => {
        this.total = 0;
        this.orders = [];
        snap.forEach((doc) => {
          let ord = doc.data();
          ord.id = doc.id;
          this.total += ord.price;
          this.orders.push(ord);
        });
      });
    },

    edit(item) {
      this.editItem = item;
      this.showOrder = true;
    },

    formatDate(value) {
      if (!value) return "";

      if (value.seconds) return moment.unix(value.seconds).format("DD/MM/YYYY");
      else return moment(value).format("DD/MM/YYYY");
    },

    formatDecimal(value) {
      if (!value || value == undefined || isNaN(value)) {
        value = 0;
      }

      return value.toLocaleString("pt", {
        style: "decimal",
        minimumFractionDigits: "2",
      });
    },

    add() {
      this.editItem = new Object();
      this.editItem.customer = firebase
        .firestore()
        .doc("/customer/" + this.customer.id);

      this.showOrder = true;
    },
    returnClick(){
      this.$router.push({ name: 'Customer' });
    },
    async toInvoiceClick() {
      const invoiceService = new InvoiceService();
      await invoiceService.makeInvoice(this.ordersToInvoice, this.customer.id);
      this.selectedsToInvoice = [];
      this.activeTab = 1;
      this.loadCustomer(this.customer.id);
    },
  },
};
</script>